import React, { Component } from 'react';
import './skills.css';
import {Button, FormGroup, FormControl,ProgressBar, Grid, Row, Col } from 'react-bootstrap';
import foto from './img/ofi.jpg';
import foto1 from './img/black1.jpg';
import foto2 from './img/_black.png';
import logo from "./img/logi.png";
import linea from "./img/linea2.png";
import boot from "./img/bootstrap.png"
import javascript from "./img/javascript.png";
import jquery from "./img/jquery.png";
import react from "./img/react.png";
import contactKely from "./img/homee.png";
import hackhaton from "./img/premios1.png";
import {BrowserRouter,Route,Switch,Redirect} from 'react-router-dom';
     
const Skills = ({ }) => {
    return (
     <Grid id="skills"> 
      <h1>HABILIDADES</h1>

      <Row>
     <Col mdOffset={6} lgOffset={6} md={6} lg={6} xs={12}>
       
        <ProgressBar className="skillbar clearfix " >
            <ProgressBar className="skillbar-title" bsStyle="warning" now={20} key={1} label={`Javascript`}/>
            <ProgressBar className="success1" now={56} key={1} />
             <div class="skill-bar-percent"><strong>Intermedio</strong></div>
        </ProgressBar>
        <ProgressBar className="skillbar clearfix " >
            <ProgressBar className="skillbar-title" bsStyle="warning" now={20} key={1} label={`React`}/>
            <ProgressBar className="success1" now={46} key={2} />
            <div class="skill-bar-percent"><strong>Básico</strong></div>
        </ProgressBar>
        <ProgressBar className="skillbar clearfix ">
            <ProgressBar className="skillbar-title" bsStyle="warning" now={20} key={1} label={`Redux`}/>
            <ProgressBar className="success1" now={40} key={3} />
            <div class="skill-bar-percent"><strong>Básico</strong></div>
        </ProgressBar>
        <ProgressBar className="skillbar clearfix " >
            <ProgressBar className="skillbar-title" bsStyle="warning" now={20} key={1} label={`jQuery`}/>
            <ProgressBar className="success1" now={33} key={4} />
            <div class="skill-bar-percent"><strong>Básico</strong></div>
        </ProgressBar>
        </Col>
        
         <Col md={6} lg={6} xs={12}>
        <ProgressBar className="skillbar clearfix " >
            <ProgressBar className="skillbar-title" bsStyle="warning" now={20} key={1} label={`HTML`}/>
            <ProgressBar className="success1" now={70} key={1} />
             <div class="skill-bar-percent"><strong>Intermedio</strong></div>
        </ProgressBar>
        <ProgressBar className="skillbar clearfix " >
            <ProgressBar className="skillbar-title" bsStyle="warning" now={20} key={1} label={`CSS`}/>
            <ProgressBar className="success1" now={60} key={2} />
            <div class="skill-bar-percent"><strong>Intermedio</strong></div>
        </ProgressBar>
        <ProgressBar className="skillbar clearfix " >
            <ProgressBar className="skillbar-title" bsStyle="warning" now={20} key={1} label={`Bootstrap`}/>
            <ProgressBar className="success1" now={60} key={3} />
            <div class="skill-bar-percent"><strong>Intermedio</strong></div>
        </ProgressBar>
        <ProgressBar className="skillbar clearfix " >
            <ProgressBar className="skillbar-title" bsStyle="warning" now={20} key={1} label={`GitHub`}/>
            <ProgressBar className="success1" now={50} key={3} />
            <div class="skill-bar-percent"><strong>Intermedio</strong></div>
        </ProgressBar>
        
        </Col>
       </Row>
</Grid>

    );

}

export default Skills;


